.admin {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  padding-top: 50px;
  .products-img {
    max-width: 50px;
    max-height: 50px;
    @media (max-width: 575.98px) {
      max-width: 35px;
      max-height: 35px;
    }
    @media (min-width: 576px) and (max-width: 767.98px) {
      max-width: 35px;
      max-height: 35px;
    }
  }
  .fa-trash {
    color: #dc3545;
    transition: all 0.2s linear;
    &:hover {
      transform: scale(1.15);
    }
  }
  .table {
    background-color: #fafafa;
    border-radius: 5px;
    thead {
      tr {
        th {
          @media (max-width: 575.98px) {
            font-size: 0.85em;
          }
          @media (min-width: 576px) and (max-width: 767.98px) {
            font-size: 0.85em;
          }
        }
      }
    }
    tbody {
      tr {
        td:nth-of-type(3) {
          max-width: 200px;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          @media (max-width: 575.98px) {
            max-width: 190px;
          }
          @media (min-width: 576px) and (max-width: 767.98px) {
            max-width: 180px;
          }
        }
        td:nth-of-type(6) {
          min-width: 100px;
        }
      }
    }
    td {
      vertical-align: middle;
    }
  }
}
