@import '../../../styles/variables/variables.scss';

.new__stories {
  background-color: #fafafa;
  border-radius: 5px;
  border: 1px solid #e1e8ed;
  .stories__listItems {
    overflow-y: hidden;
    overflow-x: scroll;
    .list__item {
      position: relative;
      min-width: 110px;
      margin-right: 10px;
      height: 170px;
      border: 2px solid $mainColor;
      border-radius: 10px;
      overflow: hidden;
      .item__pigPic {
        max-width: 110px;
        height: 100%;
        cursor: pointer;
        transition: all 0.25s ease-in-out;
        img {
          height: 100%;
          max-width: 100% !important;
        }
        &:hover {
          transform: scale(1.15);
        }
      }
      .item__smallPic {
        position: absolute;
        top: 5px;
        left: 5px;
        width: 40px;
        height: 40px;
        cursor: pointer;
        img {
          width: 100%;
          border: 2px solid rgba(255, 255, 255, 0.8);
          height: 100%;
        }
      }
      .item__name {
        width: 100%;
        height: 25px;
        background-color: rgba(0, 0, 0, 0.7);
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
        h6 {
          width: 100%;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
          font-size: 14px;
        }
      }
      .item__add {
        width: 30px;
        height: 30px;
        position: absolute;
        bottom: 85%;
        left: 50%;
        border: 1px solid white;
        border-radius: 50%;
        background-color: $mainColor;
        cursor: pointer;
        transform: translateX(-50%);
      }
    }
  }
  .stories__listItems::-webkit-scrollbar {
    display: none;
  }
}
.profileImage {
  width: 40px;
  height: 40px;
  margin-right: 5px;
}
.storyHeadr {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 17px;
}
.time {
  color: #d7d7d785;
}
.left {
  display: flex;
  align-items: center;
}
.right {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
  color: #fff;
  font-size: 1.2rem;
  li {
    cursor: pointer;
  }
}
.story_image {
  img {
    height: 67vh;
  }
}
.load {
  height: 3px;
  width: 100%;
}
